.sign__text-input {
  padding: 0;
  margin: 30px auto 0;
  width: 100%;
  min-height: 30px;
  border: 0;
  box-sizing: border-box;
  border-bottom: 2px solid rgba(204, 204, 204, 1);
  background-color: transparent;
  font-size: 14px;
  line-height: 1.21;
  font-family: inherit;
  color: white;
}

.sign__text-input:first-of-type {
  margin-top: 54px;
}

.sign__text-input:focus {
  outline: none;
  border-color: #fff;
  border-width: 3px;
}
