.sign__button {
    margin-top: 216px;
    padding: 0;
    width: 100%;
    height: 50px;
    background-color: #fff;
    color: #000;
    border: 0;
    border-radius: 2px;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.22;
    text-align: center;
    cursor: pointer;
    transition: opacity 0.2s linear;

}

.sign__button:hover {
  opacity: 0.8;
}
