.profile {
  margin: 40px auto 0;
  padding: 0;
  width: 88.125%;
  display: flex;
  align-items: center;
  gap: 30px;
}

@media screen and (min-width: 1280px) {
  .profile {
    width: 68.75%;
  }
}

@media screen and (max-width: 768px) {
  .profile {
    gap: 0;
    margin-top: 42px;
    flex-flow: column;
  }
}
